body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.background {
  /* z-index: -10; */
  overflow-y: hidden;
  width: 100vw;
  height: 100vh;
  position: fixed;
  /* background: url(./imgs/papyrus.png); */
  background-color: #17bc9b;
  /* background-color: #2D3237; */
}

.scrollContainer {
  /* width: 100vw;
  height: 100vh; */
  /* margin: 0 auto; */
  /* background-color: #17BC9B; */
  /* overflow: auto; */
  /* scroll-snap-type: x mandatory; */
  /* overflow-x: scroll; */
  display: flex;
}

.topnav {
  z-index: 1;
  display: flex;
  justify-content: space-between;
  position: relative;
  position: fixed;
  margin: 0 auto;
  padding: auto 10%;
  width: 100vw;
  height: 6vh;
  background-image: linear-gradient(#070809, #2d3237);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.logoContainer {
  margin-left: 2.5vw;
  /* width: 60em; */
  height: 100%;
  display: inline-flex;
  justify-content: space-between;
}

.logo {
  margin: auto;
  width: 2.5em;
  height: 2.5em;
  background: url(/monster-manual/static/media/treeLogo.7f3a5e62.png);
  background-size: contain;
}

.logoContainer h1 {
  padding-left: 15px;
  text-align: center;
  font-family: "IM Fell English SC", serif;
  color: white;
  margin: auto;
}

.search {
  margin: auto 2.5vw auto auto;
  padding: auto;
  width: 15vw;
  height: auto;
}

input {
  width: 100%;
  height: 100%;
  border: 5px solid black;
  border-radius: 5px;
  /* text-align: center; */
  font-size: 1.5rem;
  font-family: "IM Fell English SC", serif;
}

.searchButton {
  background-color: green;
  width: 1rem;
  height: 1rem;
}

@media screen and (max-width: 414px) {
  .logoContainer h1 {
    /* display: none; */
    font-size: 1rem;
  }

  .search {
    margin: 0;
    padding-right: 5vw;
    width: 40vw;
    height: 70%;
  }
}

.container {
  /* scroll-snap-align: center; */
  margin: 5.25vh auto;
}

.page {
  opacity: 0;
  margin: 25px 50px;
  background-size: 130% 130%;
  background-position: center;
  background-repeat: no-repeat;
  /* background-size: cover; */
  height: 850px;
  width: 600px;
  border: 2px solid black;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 1.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.sacredGeo {
  -webkit-filter: drop-shadow(0px 0px 0.1px black);
          filter: drop-shadow(0px 0px 0.1px black);
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
  color: #000;
  z-index: -1;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  width: 200px;
  height: 200px;
  background-image: url(/monster-manual/static/media/01.d7610e9e.png);
}

.title {
  padding: 5% 2.25%;
  height: 12%;
  font-family: "IM Fell English SC", serif;
  font-weight: 900;
}

.title h2 {
  font-family: "IM Fell English SC", serif;
  margin: 0 auto;
  font-size: 2.5rem;
  font-weight: 700;
  color: black;
  text-align: center;
}

.title h3 {
  font-family: "IM Fell English SC", serif;
  margin: 0 auto;
  padding: 0;
  font-size: 1.5rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: black;
  text-align: center;
  border-bottom: 3px solid black;
}

.challenge p {
  margin: 0 auto;
  padding: 0.5rem 0;
  font-family: "IM Fell English SC", serif;
  font-size: 1.1rem;
  font-weight: 700;
  color: black;
  text-align: center;
}

.pictureContainer {
  position: relative;
  margin: 0 auto;
  padding: 0;
  height: 35%;
  width: 80%;
  border-radius: 5px;
  border: 5px solid black;
  background-color: #2d3237;
  /* object-fit: contain; */
}

.picture {
  object-fit: contain;
  position: absolute;
  /* margin: 0 auto;
  padding: 0; */
  height: 100%;
  width: 100%;
  left: 0;
  right: 0;
  bottom: 0;
  /* background-color: #2D3237; */
  /* background-repeat: no-repeat; */
  /* background-size: contain; */
  /* object-fit: contain; */
  /* background-position: center; */
  /* border-radius: 5px;
  border: 1px solid black; */
  /* padding-top: 44.42%; */
}

.savingThrows {
  position: absolute;
  padding: auto;
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: 0;

  /* padding: 2% 0 0; */
  /* margin: 0 auto -2%; */
  /* border: 2px solid black; */
  border-radius: 1px;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(45, 50, 55, 1) 50%,
    rgba(0, 0, 0, 1) 100%
  );
  height: 15%;
  width: auto;
}

.savingThrows p {
  text-align: center;
  margin: 2.5% auto;
  color: whitesmoke;
  font-family: "IM Fell English SC", serif;
  font-size: 1.05rem;
  font-weight: 700;
}

.details {
  border: 2px solid black;
  border-radius: 5px;
  margin: 1.5% auto 0;
  padding: 0;
  display: flex;
  background: radial-gradient(
      ellipse farthest-corner at right bottom,
      #fedb37 0%,
      #fdb931 8%,
      #9f7928 30%,
      #8a6e2f 40%,
      transparent 80%
    ),
    radial-gradient(
      ellipse farthest-corner at left top,
      #ffffff 0%,
      #ffffac 8%,
      #d1b464 25%,
      #5d4a1f 62.5%,
      #5d4a1f 100%
    );
  width: 80%;
}

.details :nth-child(1) {
  background: rgb(0, 0, 0);
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(85, 126, 240, 1) 10%,
    rgba(90, 111, 255, 1) 50%,
    rgba(85, 126, 240, 1) 90%,
    rgba(0, 0, 0, 1) 100%
  );
}

.details :nth-child(2) {
  background: rgb(0, 0, 0);
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(240, 85, 85, 1) 10%,
    rgba(255, 90, 90, 1) 50%,
    rgba(240, 85, 85, 1) 90%,
    rgba(0, 0, 0, 1) 100%
  );
}

.details :nth-child(3) {
  background: rgb(0, 0, 0);
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 1) 0%,
    rgb(88, 240, 85) 10%,
    rgb(98, 255, 90) 50%,
    rgb(88, 240, 85) 90%,
    rgba(0, 0, 0, 1) 100%
  );
}

.details p {
  border: 2px solid #2d3237;
  border-radius: 5px;
  margin: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-family: "IM Fell English SC", serif;
  font-size: 1.2rem;
  font-weight: 900;
  border: 2px solid black;
  border-radius: 5px;
  line-height: 100%;
  width: 33%;
  height: 65px;
}

.content {
  margin: 0 auto;
  display: flex;
  border-radius: 10px;
  height: 30%;
  width: 80%;
}

.stats {
  border: 2px solid black;
  border-radius: 5px;
  background-color: #2d3237;

  background: radial-gradient(
      ellipse farthest-corner at right bottom,
      #efefef 0%,
      #d7d7d7 8%,
      #919191 30%,
      #747474 40%,
      transparent 80%
    ),
    radial-gradient(
      ellipse farthest-corner at left top,
      #d5d5d5 0%,
      #bbbbbb 8%,
      #9c9c9c 25%,
      #565656 62.5%,
      #3c3c3c 100%
    );
  line-height: 30%;
  margin: 2.5%;
  padding: 1%;
  width: 25%;
  padding: 1rem;
}

.stats ul {
  list-style-type: hebrew;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 0 0 -25%;
  font-family: "IM Fell English SC", serif;
  font-size: auto;
  font-weight: 700;
}

.stats li {
  height: 1.75em;
  width: 6.75em;
  border: 2px solid #2d3237;
  border-radius: 5px;
  background: url(/monster-manual/static/media/papyrus.b2fd3a49.png);
}

.body {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
  border: 2px solid #2d3237;
  border-radius: 5px;
  background: url(/monster-manual/static/media/papyrus.b2fd3a49.png);
  background-repeat: repeat-y;
  background-attachment: local;
  margin: 2.5%;
  padding: 5%;
  width: 65%;
  height: auto;
  overflow-y: auto;
}

.body ul {
  text-align: left;
  margin: auto 2%;
  padding: 1%;
  font-family: "IM Fell English SC", serif;
  font-size: 1rem;
  font-weight: 900;
  line-height: 150%;
}

.grow {
  transition: all 0.1s ease-in-out;
}
.grow:hover {
  -webkit-transform: scale(1.05) translateY(-5px);
          transform: scale(1.05) translateY(-5px);
  -webkit-filter: drop-shadow(5px 5px 10px #000);
          filter: drop-shadow(5px 5px 10px #000);
}
.fade-bottom {
  -webkit-animation: fade-bottom 0.25s cubic-bezier(1, -0.03, 0, 1.01);
          animation: fade-bottom 0.25s cubic-bezier(1, -0.03, 0, 1.01);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  /* animation-delay: 0s; */
}

@-webkit-keyframes fade-bottom {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5) translateY(110vh);
            transform: scale(0.5) translateY(110vh);
  }
  10% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fade-bottom {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5) translateY(110vh);
            transform: scale(0.5) translateY(110vh);
  }
  10% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

